import React, {Component} from 'react';
import {Button, Form, Tabs, Upload, message, Input, Space} from "antd";

import {InboxOutlined,UploadOutlined} from "@ant-design/icons";
import VideoUploadFrame from "./VideoUploadFrame";
import VideoContribute from "./VideoContribute";
const {TabPane}=Tabs
const { Dragger } = Upload;

class Upload_c extends Component {
    state={}

    callback=(key)=> {
        console.log(key);
    }
    normFile = (e) => {
        if (Array.isArray(e)) {
            return e;
        }
        return e && e.fileList;
    };
    formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 14 },
    };
    onFinish = (values) => {
        console.log('Received values of form: ', values);
    };

    render() {
        return (
            <Tabs defaultActiveKey="1" onChange={this.callback}>
                <TabPane tab="" key="1">
                    <VideoContribute/>
                </TabPane>
                <TabPane tab="" key="2">
                    
                </TabPane>
                <TabPane tab="" key="3">
                    <Form.Item label="">
                        <Form.Item name="dragger" valuePropName="fileList" getValueFromEvent={this.normFile} noStyle>
                            <Upload.Dragger name="files" action="/upload.do">
                                <p className="ant-upload-drag-icon">
                                    <InboxOutlined />
                                </p>
                                <Button type="primary"></Button>
                                <p> </p>
                            </Upload.Dragger>
                        </Form.Item>
                    </Form.Item>
                </TabPane>
                <TabPane tab="" key="4">
                    
                </TabPane>
                <TabPane tab="" key="5">
                    <Form.Item label="">
                        <Form.Item name="dragger" valuePropName="fileList" getValueFromEvent={this.normFile} noStyle>
                            <Upload.Dragger name="files" action="/upload.do">
                                <p className="ant-upload-drag-icon">
                                    <InboxOutlined />
                                </p>
                                <span>750*750px 5MB</span>
                                <br/>
                                <span>20 JPG/PNG/GIF/WEBP</span>
                                <Button type="primary"></Button>
                            </Upload.Dragger>
                        </Form.Item>
                    </Form.Item>
                </TabPane>
                <TabPane tab="" key="6">
                    <Form.Item label="">
                        <Form.Item name="dragger" valuePropName="fileList" getValueFromEvent={this.normFile} noStyle>
                            <Upload.Dragger name="files" action="/upload.do">
                                <p className="ant-upload-drag-icon">
                                    <InboxOutlined />
                                </p>
                                <Button type="primary"></Button>
                            </Upload.Dragger>
                        </Form.Item>
                    </Form.Item>
                </TabPane>
            </Tabs>
        )
    }
}

export default Upload_c;