import React, {Component} from 'react';
import { Tabs } from 'antd';

const { TabPane } = Tabs;

function callback(key) {
    console.log(key);
}

const Manuscript = () => (
    <Tabs defaultActiveKey="1" onChange={callback}>
        <TabPane tab="" key="1">
            <Tabs defaultActiveKey="1" onChange={callback}>
                <TabPane tab="" key="1">
                    Content of Tab Pane 1
                </TabPane>
                <TabPane tab="" key="2">
                    Content of Tab Pane 2
                </TabPane>
                <TabPane tab="" key="3">
                    Content of Tab Pane 3
                </TabPane>
                <TabPane tab="" key="4">
                    Content of Tab Pane 3
                </TabPane>
            </Tabs>
        </TabPane>
        <TabPane tab="" key="2">
            Content of Tab Pane 2
        </TabPane>
        <TabPane tab="" key="3">
            <Tabs defaultActiveKey="1" onChange={callback}>
                <TabPane tab="" key="1">
                    Content of Tab Pane 1
                </TabPane>
                <TabPane tab="" key="2">
                    Content of Tab Pane 2
                </TabPane>
                <TabPane tab="" key="3">
                    Content of Tab Pane 3
                </TabPane>
                <TabPane tab="" key="4">
                    Content of Tab Pane 3
                </TabPane>
            </Tabs>
        </TabPane>
        <TabPane tab="" key="4">
            Content of Tab Pane
        </TabPane>
        <TabPane tab="" key="5">
            <Tabs defaultActiveKey="1" onChange={callback}>
                <TabPane tab="" key="1">
                    Content of Tab Pane 1
                </TabPane>
                <TabPane tab="" key="2">
                    Content of Tab Pane 2
                </TabPane>
                <TabPane tab="" key="3">
                    Content of Tab Pane 3
                </TabPane>
                <TabPane tab="" key="4">
                    Content of Tab Pane 3
                </TabPane>
            </Tabs>
        </TabPane>
        <TabPane tab="" key="6">
            <Tabs defaultActiveKey="1" onChange={callback}>
                <TabPane tab="" key="1">
                    Content of Tab Pane 1
                </TabPane>
                <TabPane tab="" key="2">
                    Content of Tab Pane 2
                </TabPane>
                <TabPane tab="" key="3">
                    Content of Tab Pane 3
                </TabPane>
                <TabPane tab="" key="4">
                    Content of Tab Pane 3
                </TabPane>
            </Tabs>
        </TabPane>
    </Tabs>
);

export default Manuscript;