import React, {Component} from 'react';
import { Tabs,Select } from 'antd';

const { Option } = Select;

function onChange(value) {
    console.log(`selected ${value}`);
}

function onSearch(val) {
    console.log('search:', val);
}
function handleChange(value) {
    console.log(`selected ${value}`);
}
const { TabPane } = Tabs;

function callback(key) {
    console.log(key);
}

const Caption = () => (
    <Tabs defaultActiveKey="1" onChange={callback}>
        <TabPane tab="" key="1">
            <Select
                showSearch
                placeholder=""
                optionFilterProp="children"
                onChange={onChange}
                onSearch={onSearch}
                filterOption={(input, option) =>
                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                }
            >
                <Option value="video_id1">1</Option>
                <Option value="video_id2">2</Option>
                <Option value="video_id3">3</Option>
            </Select>
            <Select defaultValue="captionCategory1" style={{ width: 120 }} onChange={handleChange}>
                <Option value="captionCategory1"></Option>
                <Option value="captionCategory2"></Option>
                <Option value="captionCategory3"></Option>
            </Select>
        </TabPane>
        <TabPane tab="" key="2">
            <Tabs defaultActiveKey="1" onChange={callback}>
                <TabPane tab="" key="1">
                    Content of Tab Pane 1
                </TabPane>
                <TabPane tab="" key="2">
                    Content of Tab Pane 2
                </TabPane>
                <TabPane tab="" key="3">
                    Content of Tab Pane 3
                </TabPane>
                <TabPane tab="" key="4">
                    Content of Tab Pane 4
                </TabPane>
                <TabPane tab="" key="5">
                    Content of Tab Pane 5
                </TabPane>
            </Tabs>
        </TabPane>
    </Tabs>
);

export default Caption;