import React, {Component} from 'react';
import Recommend from "../../components/Recommend";
import ShowList from "../../components/ShowList";
import Catalog from "../../components/Catalog";

class Index extends Component {
    render() {
        return (
            <div>
                <Catalog/>
                <Recommend/>
                <ShowList id={`competition`}></ShowList>
                <ShowList id={`live`}></ShowList>
                <ShowList id={`cartoon`}></ShowList>
                <ShowList id={`anime`}></ShowList>
                <ShowList id={`music`}></ShowList>
                <ShowList id={`dance`}></ShowList>
                <ShowList id={`game`}></ShowList>
                <ShowList id={`knowledge`}></ShowList>
                <ShowList id={`sports`}></ShowList>
                <ShowList id={`foods`}></ShowList>
            </div>
        );
    }
}

export default Index;