import React, {Component} from 'react';
import {FireOutlined} from '@ant-design/icons'
import {Row, Col} from 'antd';
import {Link} from 'react-router-dom'
import './index.css'
import CatalogCol from "./CatalogCol";
class Catalog extends Component {
    render() {
        return (
            <div id={`catalog`} className={`catalog`}>
                <Row justify="center">
                    {/*<Col span={1} className={`channel-icons`}></Col>*/}
                    <Col span={6} className={`channel-icons`}>
                        <Row  gutter={[0,8]} justify="center">
                            <Col span={5}>
                                <span></span>
                            </Col>
                            <Col span={5}>
                                <FireOutlined/>
                                <span></span>
                            </Col>
                            <Col span={5}>
                                <span></span>
                            </Col>
                        </Row>
                    </Col>
                    <Col span={12} className={`channel-items__left`}>
                        <Row gutter={[45, 10]} justify="space-around">
                            <CatalogCol>
                                <Link to={`/anime`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/guochuang`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/variety`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/cartoon`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/documentary`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/dance`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/ent`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/tech`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/foods`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/movie`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/music`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/kichiku`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/game`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/tv`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/cinephile`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/knowledge`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/information`} target={`_blank`}></Link>
                            </CatalogCol>
                            <CatalogCol>
                                <Link to={`/more`} target={`_blank`}></Link>
                            </CatalogCol>
                        </Row>
                    </Col>
                    <Col span={4} className={`channel-items__right`}>
                        <Row gutter={[25,8]} justify="center">
                            <Col span={2/3}>
                                
                            </Col>
                            <Col span={2/3}></Col>
                            <Col span={2/3}></Col>

                            <Col span={2/3}></Col>
                            <Col span={2/3}></Col>
                            <Col span={2/3}></Col>
                        </Row>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Catalog;